.what {
    display: flex;
}

.what__left{
    display:flex;
    flex-flow:column;
}

.what__left .top{
    display:flex;
}

.what__left .top__left{
    display:flex;
    flex-flow:column;
}

.what__left .top__left .upper{
    display:flex;
    justify-content:flex-end;
}

.what__left .top__left .lower{
    display:flex;
}

.what__left .top__left .lower__left{
    display:flex;
    flex-flow:column;
    align-items:flex-end;
}

.what__left .top__left .lower__left .row1{
    display:flex;
}
.what__left .top__left .lower__right img{
    width: 255px;
    height: 511px;
    margin: 4px 0 23px 21px;
    object-fit: fill;
    border-radius: 15px;
}

.what__left .top__right{
    display:flex;
    flex-flow:column;
}

.what__left .top__right .upper{
    display:flex;
    justify-content:flex-end;
}

.what__left .top__right .upper__left img{
    width: 262px;
    height: 335px;
    margin: 20px 2px 17px 0px;
    object-fit: fill;
    border-radius: 15px;
}

.what__left .top__right .upper__right{
    display:flex;
    flex-flow:column;
    justify-content:flex-end;
}

.what__left .top__right .lower img{
    width: 401px;
    height: 331px;
    margin: 0px 25px 25px 21px;
    object-fit: fill;
    border-radius: 15px;
}
.what__left .bottom{
    display:flex;
    flex-flow:row;
    justify-content:flex-end;
}

.what__rightContentBottom{
    display:flex;
    justify-content:flex-end;
}
